import React, { useEffect, useState } from "react";
//import { Component } from 'react';

const valueExample = 10;

window.recordTime = 0;

function randomNumberInRange(min, max) {
	return Math.floor(Math.random() * (max - min + 1)) + min;
}

function createExample() {
	const ops = ['+', '-', '*', '/'];
	const randomOps = ops[Math.floor(Math.random()*ops.length)]; 

	if (randomOps === '+' || randomOps === '-') {
		const num1 = randomNumberInRange(0, 50);
		const num2 = randomNumberInRange(0, 50);
		if (num1 > num2) {
			return num1+randomOps+num2
		} else {
			return num2+randomOps+num1
		}
	}
	if (randomOps === '*') {
		const num1 = randomNumberInRange(0, 10);
		const num2 = randomNumberInRange(0, 20);
		return num1+randomOps+num2
	}
	if (randomOps === '/') {
		const num1 = randomNumberInRange(1, 20);
		const num2 = randomNumberInRange(1, 20);
		const dev = num1*num2;
		return dev+randomOps+num2
	}
}


//const trueExample = createExample();

function App() {

	const [state, setState] = useState({
		textExample: createExample(),
		numExample: valueExample,
		result: '',
		wishText: 'Натисніть Старт',
		startButton: 'Старт',
		buttonDisable: true
	});

	const textResult = eval(state.textExample).toString();
	
	//Тамйер
	const [seconds, setSeconds] = useState(0);

	useEffect(() => {
		if (state.startButton === 'Стоп') {
			setTimeout(() => setSeconds(seconds + 1), 1000);
		} else {
			setSeconds(0)
		};
	});
		
	//Цифрова клавіатура
	const createDigits = () => {
		const digits = [];

		for (let i = 1; i < 10; i++) {
			digits.push(
				<button onClick={() => updateResult(i.toString())} key={i} disabled={ state.buttonDisable }>
					{i}
				</button>
			)
		}
		return digits;
	}
	
	const deleteLast = () => {
		if (state.result === '') {
			return;
		}
		const value = state.result.slice(0, -1);
		setState({
			...state,			
			result: value
		})
	}

	const updateResult = value => {
		setState({
			...state,			
			result: state.result+value
		})

		const result = state.result+value;
		if (result !== '' && result === textResult) {
			setState({
				...state,
				textExample: createExample(),
				numExample: state.numExample - 1,
				result: ''
			})
		}
		
	}
	
	const calculate = () => {
		if (state.startButton === 'Старт') {
			setState({
				...state,
				textExample: createExample(),
				result: '',
				wishText: '',
				startButton: 'Стоп',
				buttonDisable: false
			})
			setSeconds(0);
		} else {
			setState({
				...state,
				numExample: valueExample,
				startButton: 'Старт',
				buttonDisable: true
			})
			setSeconds(0);
		}
	}

	if (state.numExample === parseInt('0')) {
		
		setState({
			...state,
			numExample: valueExample,
			result: seconds+' c',
			wishText: 'Ваш час: ',
			buttonDisable: true,
			startButton: 'Старт',			
		});

		if ((seconds < window.recordTime) || (window.recordTime === 0)) {
			window.recordTime = seconds;
		}

		setSeconds(0);
	}
	
	return (
		<div className="App">
			<div className="calculator">
				<div className="head">
					<button className="example">{ state.numExample }</button>
					<button className="timeEx">{ seconds }</button>
					<button className="record">{ window.recordTime }</button>
				</div>
				<div className="head-text">
					<div>Приклад</div>
					<div>Час</div>
					<div>Рекорд</div>
				</div>

				<div className="display">
					{ state.wishText ? state.wishText : state.textExample+'=' }
					<span>{ state.result }</span>
				</div>

				<div className="digits">
					{ createDigits() }
					<button onClick={() => updateResult('0')} disabled={ state.buttonDisable }>0</button>
					<button onClick={deleteLast} disabled={ state.buttonDisable }>
						<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-backspace-fill" viewBox="0 0 16 16">
						<path d="M15.683 3a2 2 0 0 0-2-2h-7.08a2 2 0 0 0-1.519.698L.241 7.35a1 1 0 0 0 0 1.302l4.843 5.65A2 2 0 0 0 6.603 15h7.08a2 2 0 0 0 2-2V3zM5.829 5.854a.5.5 0 1 1 .707-.708l2.147 2.147 2.146-2.147a.5.5 0 1 1 .707.708L9.39 8l2.146 2.146a.5.5 0 0 1-.707.708L8.683 8.707l-2.147 2.147a.5.5 0 0 1-.707-.708L7.976 8 5.829 5.854z"/>
						</svg>
					</button>
					<button id="operators" onClick={calculate}>{ state.startButton }</button>
				</div>
			</div>
		</div>
	);
}

export default App; 
